.category-list li.active {
  background-color: #eee;
  font-weight: bold;
}

/* ... 其他樣式保持不變 ... */

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  /* 使用 gap 來代替 margin，更現代 */
  gap: 8px; 
}

/* 這是針對大於 768px 的設備（電腦版） */
@media (min-width: 769px) {
  .product-list {
    /* 確保在電腦上是正常的 flex 行為 */
    flex-direction: row; 
  }
}

/* 這是針對小於或等於 768px 的設備（手機版） */
@media (max-width: 768px) {
  .product-list {
    /* 在手機上切換為垂直排列 */
    flex-direction: column;
    align-items: center;
  }
}
.product-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  width: 100%;
}

.pagination {
  text-align: center;
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}

.page-button {
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  background-color: #e60023;
  color: #fff;
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.page-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}
</style>